import React, { useState } from 'react'
import ReactDOM from 'react-dom';
import Deshboard from './Deshboard'
import Board from './Board';

function Insertion_sort() {
    const [showArray, setshowArray] = useState([]);

    const sortarray = (array) => {
        let count = 0;
        for (var i = 0; i < array.length - 1; i++) {
            for (var j = 0; j < (array.length - i - 1); j++) {
                (function (i, j) {
                    setTimeout(function () {
                        if (array[j] > array[j + 1]) {
                            var temp = array[j];
                            array[j] = array[j + 1];
                            array[j + 1] = temp;
                        }
                        // console.log(array);
                        var data = [];
                        count++;
                        for (var k = 0; k < array.length; k++) {
                            data.push({
                                id: k,
                                item: array[k],
                                color: (i == array.length - 2) ? "black" : ((j == k || (j + 1) == k) ? "red" : "black"),
                                bgc: (i == array.length - 2) ? "green" : ((k > (array.length - i - 1)) ? "green" : (j == k || (j + 1) == k) ? "black" : "white")
                            });
                        }
                        { ReactDOM.render(<Board arr={data} />, document.querySelector('.sh')) }
                    }, 1000 * (((array.length * i) - ((i * (i + 1)) / 2) + j) + 1));
                })(i, j);
            }
        }
    }


    return (<>
        <Deshboard sort_type= "Insertion Sort"
            sort={sortarray}
            showArray={showArray} setshowArray={setshowArray} />
    </>);
}

export default Insertion_sort;